.signin {
    height: calc(110vh - 30px - 130px);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: linear-gradient(
    rgba(10, 10, 10, 0.5),
    rgba(10, 10, 10, 0.5)
    ),
    url("https://media.istockphoto.com/photos/film-reels-on-black-background-movie-video-and-cinema-prodaction-and-picture-id1336937059?k=20&m=1336937059&s=170667a&w=0&h=aaFBLnJU5BICv5ORClwmeL32ZQhueE7ZMtsrNZMFRH4=");
    background-size: cover;
}

.signinForm {
    display: flex;
    flex-direction: column;
    padding-top: 20px;
}

.signinInput {
    width: 300px;
    padding: 5px;
    border: none;
    background-color: transparent!important;
    font-size: 13px;
    height: 20px;
    border-radius: 23.5px;
    border: 2px solid #797d7a!important;
    box-shadow: 0 0 4px rgb(219 146 10);
    text-align: center;
    color: #fff;
    margin: 10px auto;
    background: linear-gradient(black, transparent);
}

.signinForgot {
    border: none;
    background: transparent;
    color: white;
    padding: 10px;
    text-decoration: underline;
    cursor: pointer;
}

.signinSignIn {
    margin-top: 10px;
    cursor: pointer;
    background-color: rgb(170, 9, 9);
    border: none;
    color:white;
    border-radius: 30px;
    padding: 10px;
    box-shadow: 0 0 4px rgb(230, 218, 218);
    width: 100px;
    align-self: center;
    letter-spacing: 2px;
    font-weight: 600!important;
}

.signinSignUp {
    border: none;
    background: transparent;
    color: white;
    padding: 20px;
    text-decoration: underline;
    cursor: pointer;
}

.signinTitle {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: #d8c87d;
    font-size: 40px;
    padding-top: 20px;
}